\n    opacity: 0;\n  }\n}\n@keyframes rippleOff {\n  0% {\n    opacity: 0;\n  }\n  50% {\n    opacity: 0.2;\n  }\n  100% {\n    opacity: 0;\n  }\n}\n","\n@mixin radio-color($color, $opacity){\n  & ~ .check,\n  & ~ .circle  {\n    opacity: $opacity;\n  }\n\n  & ~ .check {\n    background-color: $color;\n  }\n\n  & ~ .circle  {\n    border-color: $color;\n  }\n}\n\n.form-check{\n    .form-check-label {\n        cursor: pointer;\n        padding-left: 25px;\n        position: relative;\n        @include mdb-label-color-toggle-focus();\n\n    span {\n        display: block;\n        position: absolute;\n        left: -1px;\n        top: -1px;\n        transition-duration: 0.2s;\n    }\n    .circle {\n        border: 1px solid $mdb-radio-color-off;\n        height: 15px;\n        width: 15px;\n        border-radius: 100%;\n        top: 1px;\n\n        .check {\n            height: 15px;\n            width: 15px;\n            border-radius: 100%;\n            background-color: $mdb-radio-color-on;\n            @include transform-scale3d(unquote('0,0,0'));\n\n\n        }\n    }\n\n\n    .form-check-input:not(:checked) ~ .check:after {\n        @include animation(rippleOff 500ms);\n    }\n\n    .form-check-input:checked ~ .check:after {\n        @include animation(rippleOff 500ms);\n    }\n\n  }\n\n    .form-check-input {\n        opacity: 0;\n        height: 0;\n        width: 0;\n        overflow: hidden;\n\n        &:checked {\n            @include radio-color($mdb-radio-color-on, 1);\n        }\n        &:checked ~ .circle .check {\n            @include transform-scale3d(unquote('0.65, 0.65, 1'));\n        }\n    }\n\n    .form-check-input[disabled] {\n\n        // light theme spec: Disabled: #000000, Opacity  26%\n        @include radio-color($black, 0.26);\n\n        & + .circle .check{\n            background-color: $black;\n        }\n    }\n\n\n    .form-check-sign{\n        vertical-align: middle;\n        position: relative;\n        top: -2px;\n        float: le